class ActionProvider {
    constructor(createChatBotMessage, setStateFunc) {
      this.createChatBotMessage = createChatBotMessage;
      this.setState = setStateFunc;
    }

    handleOrder = () => {
        const message = this.createChatBotMessage(
          "y l hng dn mua hng cho Anh (ch) ",
          {
            widget: "OrderLinks",
          }
        );
    
        this.updateChatbotState(message);
    };

    handleIntroduce = () => {
        const message = this.createChatBotMessage(
          "y l phn gii thiu Website ca chng em ",
          {
            widget: "IntroduceLinks",
          }
        );
    
        this.updateChatbotState(message);
    };

    handleExchange = () => {
        const message = this.createChatBotMessage(
          "y l phn hng dn i tr hng ",
          {
            widget: "ExchangeLinks",
          }
        );
    
        this.updateChatbotState(message);
    };
    
    greet() {
      const greetingMessage = this.createChatBotMessage("Em cho anh (ch)  ");
      this.updateChatbotState(greetingMessage)
    }

    greet1() {
        const greetingMessage = this.createChatBotMessage("m cht cha my gi :v ");
        this.updateChatbotState(greetingMessage)
    }
    
    updateChatbotState(message) {
   
  // NOTE: This function is set in the constructor, and is passed in      // from the top level Chatbot component. The setState function here     // actually manipulates the top level state of the Chatbot, so it's     // important that we make sure that we preserve the previous state.
     this.setState(prevState => ({
          ...prevState, messages: [...prevState.messages, message]
      }))
    }
  }
  
export default ActionProvider;